<template>
  <div class="bracelet-page">
    <h1></h1>
    <h3></h3>
    <div v-for="(bracelet, index) in configuredBraceletList" :key="index" class="row bracelet">
      <eva-icon name="bluetooth" fill="#00e"></eva-icon>
      <span>{{index + 1}}: </span>
      <div class="mac">{{bracelet.mac}}</div>
      <div>
        <el-button size="mini" type="primary" title="" icon="el-icon-edit" @click="onEditBtnClick(bracelet.id, bracelet)"></el-button>
        <el-button size="mini" type="danger" title="" icon="el-icon-delete" @click="onDeleteBtnClick(bracelet.id)"></el-button>
      </div>
    </div>
    <el-button v-if="configuredBraceletList.length < 2" type="primary" @click="onAddBtnClick" class="add btn" icon="el-icon-plus" size="small"></el-button>
    <el-divider></el-divider>
    <div>
      <el-button type="primary" class="scan btn" @click="onScanBtnClick" :loading="isScanning"></el-button>
    </div>
    <div class="wifi-list" v-if="showBraceletScanResult">
      <h3></h3>
      <el-table
        :data="scannedBraceletList"
        style="width: 100%">
        <el-table-column width="50">
          <template slot-scope="scope">
            <eva-icon name="bluetooth" fill="#00e">{{scope.row.name}}</eva-icon>
          </template>
        </el-table-column>
        <el-table-column
          prop="mac"
          label="MAC">
        </el-table-column>
        <el-table-column
          prop="mac" label="" width="80">
          <template slot-scope="scope">
            <span>{{configuredBraceletList.filter(item => item.mac === scope.row.mac).length ? '' : ''}}</span>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-dialog :title="dialogTitle" 
      :visible.sync="dialogConfig.show" 
      :close-on-click-modal="false" width="80%"
      @closed="onDialogClose">
        <el-form :model="dialogConfig" :rules="dialogRules" ref="dialog">
        <el-form-item label="MAC" prop="mac">
          <el-input v-model="dialogConfig.mac" placeholder="MAC"></el-input>
        </el-form-item>
      </el-form>      
      <div slot="footer">
        <el-button type="primary" @click="onConfirmBtnClick" :loading="isUpdating"></el-button>
        <el-button type="danger" @click="onCancelBtnClick"></el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import braceletQuery from '@/rest/braceletQuery';

export default {
  data() {
    return {
      configuredBraceletList: [],
      dialogConfig: {
        mode: 'NEW', // NEW, EDIT
        mac: '',
        id: '',
        show: false
      },
      dialogRules: {
        mac: [
          { required: true, message: 'MAC', trigger: 'blur' },
          { 
            validator: (rule, value, callback) => {
              if (value.length !== 12) {
                callback(new Error('12MAC'));
              } else {
                callback();
              }
            }, 
            trigger: 'blur'
          }
        ]
      },
      isUpdating: false,
      scannedBraceletList: [],
      operatingMac: '',
      showBraceletScanResult: false,
      isScanning: false
    };
  },
  computed: {
    dialogTitle() {
      return this.dialogConfig.mode === 'NEW' ? '' : '';
    }
  },
  methods: {
    onAddBtnClick() {
      this.dialogConfig.id = '';
      this.dialogConfig.mac = '';
      this.dialogConfig.mode = 'NEW';
      this.dialogConfig.show = true;
    },
    onCancelBtnClick() {
      this.dialogConfig.show = false;
    },
    async onConfirmBtnClick() {
      this.$refs.dialog.validate(async valid => {
        if (valid) {
          try {
            this.isUpdating = true;
            if (this.dialogConfig.mode === 'NEW') {
              await braceletQuery.addBracelet(this.dialogConfig.mac);
            } else {
              await braceletQuery.updateBracelet(this.dialogConfig.id, this.dialogConfig.mac);
            }            
            this.isUpdating = false;
            this.dialogConfig.show = false;
            this.fetchConfiguredBraceletList();
            this.$message({
              message: `${this.dialogConfig.mode === 'NEW' ? '' : ''}`,
              type: 'success'
            });
          } catch {
            this.isUpdating = false;
            this.$message({
              message: `${this.dialogConfig.mode === 'NEW' ? '' : ''}`,
              type: 'error'
            });
          }          
        }
      });
    },
    onEditBtnClick(id, bracelet) {
      this.dialogConfig.mode = 'EDIT';
      this.dialogConfig.show = true;
      this.dialogConfig.mac = bracelet.mac;
      this.dialogConfig.id = bracelet.id;
    },
    onDeleteBtnClick(id) {
      this.$confirm('', '', {
        distinguishCancelAndClose: true,
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        try {
          this.isUpdating = true;
          await braceletQuery.deleteBracelet(id);
          this.isUpdating = false;
          this.$message({
            message: '',
            type: 'success'          
          });
          this.fetchConfiguredBraceletList();
        } catch {
          this.$message({
            message: '',
            type: 'error'          
          });
        }  
      });
    },
    onDialogClose() {
      this.$refs.dialog.resetFields();
    },
    async fetchConfiguredBraceletList() {
      try {
        this.configuredBraceletList = await braceletQuery.getConfiguredBraceletList();
      } catch {
        this.$message({
          message: '',
          type: 'error'
        });
      } 
    },
    async onScanBtnClick() {
      try {
        this.isScanning = true;
        this.scannedBraceletList = await braceletQuery.getScannedBraceletList();
        this.showBraceletScanResult = true;
      } catch {
        this.$message({
          message: '',
          type: 'error'
        });
      }
      this.isScanning = false;
    },
    onSelectBtnClick(mac) {
      this.operatingMac = mac;
    }
  },
  created() {
    this.fetchConfiguredBraceletList();  
  }
};
</script>

<style lang="scss" scoped>
.bracelet-page {
  .bracelet {
    align-items: center;
    flex-wrap: wrap;
    line-height:48px;
    .mac {
      width: 120px;
      margin: 0 8px;
    }
  }
  .add, .scan {
    margin-top:24px;
  }
}
</style>
